import React from "react"
import EmploymentProvinceChart from "./employment-province-chart"
import "./employment-province.css"

function EmploymentProvinceLayout({ children }) {
  return <div>{children}</div>
}

export default function EmploymentProvince({ dataset }) {
  return (
    <EmploymentProvinceLayout>
      <section className="Employment-Province">
        <EmploymentProvinceChart data={dataset} />
      </section>
    </EmploymentProvinceLayout>
  )
}
