import React from "react"
import EmploymentIndustryChart from "./employment-industry-chart"
import "./employment-industry-layout.css"

function EmploymentIndustryLayout({ children }) {
  return <div>{children}</div>
}

export default function EmploymentIndustry({ dataset }) {
  return (
    <EmploymentIndustryLayout>
      <section className="Employment-Industry">
        <EmploymentIndustryChart data={dataset.filter(d => d.year >= 2019)} />
      </section>
    </EmploymentIndustryLayout>
  )
}
