import React, { useEffect, useState } from "react"
import * as d3 from "d3"

export default function UnploymentProvinceChart({ data }) {
  const [toggle, setToggle] = useState("Unemployed")

  useEffect(() => {
    // set the dimensions and margins of the graph
    var margin = { top: 20, right: 30, bottom: 30, left: 40 },
      width = 700,
      height = 300

    d3.select("#unployment_province").append("svg").remove()

    let svg = d3
      .select("#unployment_province")
      .append("svg")
      .attr("viewBox", [
        0,
        0,
        width + 2 * margin.right,
        height + 3 * margin.bottom,
      ])

    let chartGroup = svg
      .append("g")
      .attr("transform", `translate(${margin.left},${margin.top})`)

    // let dataset = data;

    let groupingUnemployedByDate = data
      .filter(d => d.category === toggle)
      .reduce((prev, cur) => {
        if (!prev[cur.date]) {
          prev[cur.date] = []
        }

        prev[cur.date].push({ [cur.province]: cur.unemployed, date: cur.date })

        return prev
      }, {})

    let dataset = Object.keys(groupingUnemployedByDate).map(key => {
      let groupByDate = groupingUnemployedByDate[key].reduce((prev, cur) => {
        if (!prev[cur.date]) {
          prev[cur.date] = []
        }

        Object.assign(prev[cur.date], cur)

        return prev
      }, {})

      return {
        Date: key,
        "Eastern Cape": groupByDate[key]["Eastern Cape"],
        "Free State": groupByDate[key]["Free State"],
        Gauteng: groupByDate[key]["Gauteng"],
        "KwaZulu-Natal": groupByDate[key]["KwaZulu-Natal"],
        Limpopo: groupByDate[key]["Limpopo"],
        Mpumalanga: groupByDate[key]["Mpumalanga"],
        "North West": groupByDate[key]["North West"],
        "Northern Cape": groupByDate[key]["Northern Cape"],
        "Western Cape": groupByDate[key]["Western Cape"],
      }
    })

    let keys = dataset.length && Object.keys(dataset[0]).slice(1)

    let stack = d3
      .stack()
      .keys(keys)
      .order(d3.stackOrderNone)
      .offset(d3.stackOffsetNone)

    let series = stack(dataset)

    dataset.forEach(d => (d.Date = new Date(d.Date)))

    let xScale = d3
      .scaleTime()
      .domain(d3.extent(dataset, d => d.Date))
      .range([0, width])

    let addition = toggle !== "Employed" ? 900 : 5000

    let yScale = d3
      .scaleLinear()
      .domain([0, d3.max(series, d => d[d.length - 1][1]) + addition])
      .range([height, 0])

    let colors = d3
      .scaleSequential()
      .domain([0, 10])
      .interpolator(d3.interpolateRainbow)

    let xAxis = d3.axisBottom(xScale).tickPadding(10).ticks(5)
    // .tickFormat((d) => parseInt(d));
    // .tickValues([2009, 2011, 2013, 2015, 2017, 2019]);

    let yAxis = d3
      .axisLeft(yScale)
      .tickFormat(d => parseInt(d / 1000))
      .tickPadding(10)
      .ticks(5)

    let xAxisGroup = chartGroup
      .append("g")
      .attr("class", "xAxisGroup")
      .attr("transform", `translate(${0},${height + margin.top})`)

    let yAxisGroup = chartGroup
      .append("g")
      .attr("class", "yAxisGroup")
      .attr("transform", `translate(${0},${0})`)

    xAxis(xAxisGroup)
    yAxis(yAxisGroup)

    let xAxiGrids = d3
      .axisBottom(xScale)
      .tickFormat("")
      .tickSize(-height - margin.top)
      .ticks(7)
    let yAxisGrids = d3
      .axisLeft(yScale)
      .tickFormat("")
      .ticks(5)
      .tickSize(-width)

    let xAxisGridsGroup = chartGroup
      .append("g")
      .attr("class", "xAxisGridsGroup")
      .attr("transform", `translate(${0},${height + margin.top})`)

    let yAxisGridsGroup = chartGroup
      .append("g")
      .attr("class", "xAxisGridsGroup")
      .attr("transform", `translate(${0},${0})`)

    xAxiGrids(xAxisGridsGroup)
    yAxisGrids(yAxisGridsGroup)

    const stackedGroup = chartGroup
      .append("g")
      .attr("class", "stackedGroup")
      .attr("transform", `translate(${0},${0})`)

    let pathGroup = stackedGroup
      .selectAll("path")
      .data(series)
      .enter()
      .append("path")
      .attr("fill", (d, i) => colors(i))
      .attr("stroke", "#f9f9f9")
      .attr("stroke-width", ".4")
      .attr("opacity", 0.75)
      .classed("area", true)
      .attr(
        "d",
        d3
          .area()
          .x(d => xScale(d.data.Date))
          .y0(d => yScale(0))
          .y1(d => yScale(0))
      )

    pathGroup
      .transition()
      .duration(500)
      .ease(d3.easeQuadIn)
      .attr(
        "d",
        d3
          .area()
          .x(d => xScale(d.data.Date))
          .y0(d => yScale(d[0]))
          .y1(d => yScale(d[1]))
      )
  }, [data, toggle])

  return (
    <div id="unployment_province" className="chart">
      <header>
        <h4>
          Labour force characteristics by province - Expanded definition of
          unemployment (2010 - 2020)
        </h4>
        <ul>
          <li
            style={
              toggle === "Unemployed"
                ? { color: "white", backgroundColor: "black" }
                : null
            }
            onClick={() => setToggle("Unemployed")}
            aria-hidden="true"
          >
            Unemployed
          </li>
          <li
            style={
              toggle === "Employed"
                ? { color: "white", backgroundColor: "black" }
                : null
            }
            onClick={() => setToggle("Employed")}
            aria-hidden="true"
          >
            Employed
          </li>
          <li
            style={
              toggle === "Not economically active"
                ? { color: "white", backgroundColor: "black" }
                : null
            }
            onClick={() => setToggle("Not economically active")}
            aria-hidden="true"
          >
            Not economically active
          </li>
        </ul>
      </header>
    </div>
  )
}
