import React, { useEffect, useState } from "react"
import * as d3 from "d3"

export default function LabourCharacteristicProvinceChart({ data }) {
  const [toggleChange, setToggleChange] = useState("Gauteng")
  useEffect(() => {
    // set the dimensions and margins of the graph
    var margin = { top: 20, right: 30, bottom: 30, left: 50 },
      width = 700,
      height = 300

    d3.select("#characteristic_province").append("svg").remove()

    let svg = d3
      .select("#characteristic_province")
      .append("svg")
      .attr("viewBox", [
        0,
        0,
        width + 2 * margin.right,
        height + 3 * margin.bottom,
      ])

    let chartGroup = svg
      .append("g")
      .attr("transform", `translate(${margin.left},${margin.top})`)

    // let dataset = data;

    let groupByProvinceQuarterYear = data.reduce((prev, cur) => {
      let concatQuarterWithYearAndProvince =
        cur.province + " " + cur.quarter + cur.date
      if (!prev[concatQuarterWithYearAndProvince]) {
        prev[concatQuarterWithYearAndProvince] = []
      }

      prev[concatQuarterWithYearAndProvince].push(cur)

      return prev
    }, {})

    let labour_characteristics = Object.keys(groupByProvinceQuarterYear).map(
      key => {
        let employed = groupByProvinceQuarterYear[key][0].unemployed
        let unemployed = groupByProvinceQuarterYear[key][1].unemployed
        let not_active = groupByProvinceQuarterYear[key][2].unemployed

        let labour_force = employed + unemployed
        let population = labour_force + not_active

        return {
          Province: groupByProvinceQuarterYear[key][1].province,
          Quarter: groupByProvinceQuarterYear[key][1].quarter,
          Category: key,
          unemployed,
          employed,
          Date: groupByProvinceQuarterYear[key][1].date,
          "Labour Force": labour_force,
          "Unemployment Rate": (unemployed / labour_force) * 100,
          "Employed / population ratio (Absorption)":
            (employed / population) * 100,
        }
      }
    )

    let dataset = labour_characteristics
      //   .filter((d) => d.Province === toggleChange)
      .slice(-labour_characteristics.length + 1)
      .map((d, i) => {
        // console.log(d["Unemployment);

        return {
          province: d.Province,
          quarter: d.Quarter,
          date: d.Date,
          unemployment_rate: d["Unemployment Rate"],
          employment_rate: d["Employed / population ratio (Absorption)"],
          //   unemployment_rate: Number(
          //     (
          //       d["Unemployment Rate"] -
          //       labour_characteristics[i]["Unemployment Rate"]
          //     ).toFixed(1)
          //   ),
          //   employment_rate: Number(
          //     (
          //       d["Employed / population ratio (Absorption)"] -
          //       labour_characteristics[i][
          //         "Employed / population ratio (Absorption)"
          //       ]
          //     ).toFixed(1)
          //   ),
        }
      })
      .filter(d => d.province === toggleChange)

    let xScale = d3
      .scaleLinear()
      .domain([0, d3.max(dataset, d => d.employment_rate) + 30])
      .range([0, width])

    const kernelDensityEstimator = (kernel, X) => {
      return V => {
        return X.map(x => {
          return [x, d3.mean(V, v => kernel(x - v))]
        })
      }
    }

    const kernelEpanechnikov = k => {
      return v => {
        return Math.abs((v /= k)) <= 1 ? (0.75 * (1 - v * v)) / k : 0
      }
    }

    let kde = kernelDensityEstimator(kernelEpanechnikov(7), xScale.ticks(60))

    const densityEmployment = kde(dataset.map(d => d.employment_rate))
    const densityUnemployment = kde(dataset.map(d => d.unemployment_rate))

    let yScale = d3.scaleLinear().domain([0, 0.1]).range([height, 0])

    let xAxis = d3.axisBottom(xScale).tickPadding(10).ticks(5)
    let yAxis = d3
      .axisLeft(yScale)
      //   .tickFormat((d) => parseInt(d / 1000))
      .tickPadding(10)
      .ticks(5)

    let xAxisGroup = chartGroup
      .append("g")
      .attr("class", "xAxisGroup")
      .attr("transform", `translate(${0},${height + margin.top})`)

    let yAxisGroup = chartGroup
      .append("g")
      .attr("class", "yAxisGroup")
      .attr("transform", `translate(${0},${0})`)

    xAxis(xAxisGroup)
    yAxis(yAxisGroup)

    let xAxiGrids = d3
      .axisBottom(xScale)
      .tickFormat("")
      .tickSize(-height - margin.top)
      .ticks(7)
    let yAxisGrids = d3
      .axisLeft(yScale)
      .tickFormat("")
      .ticks(5)
      .tickSize(-width)

    let xAxisGridsGroup = chartGroup
      .append("g")
      .attr("class", "xAxisGridsGroup")
      .attr("transform", `translate(${0},${height + margin.top})`)

    let yAxisGridsGroup = chartGroup
      .append("g")
      .attr("class", "xAxisGridsGroup")
      .attr("transform", `translate(${0},${0})`)

    xAxiGrids(xAxisGridsGroup)
    yAxisGrids(yAxisGridsGroup)

    // Plot the area
    let densityUnemploymentGroup = chartGroup
      .append("g")
      .attr("class", "densityUnemploymentGroup")
      .attr("transform", `translate(${0},${0})`)

    let pathUnemploymentGroup = densityUnemploymentGroup
      .append("path")
      .datum(densityUnemployment)
      .attr("fill", "orange")
      .attr("opacity", ".7")
      .attr("stroke", "#d8d8d8")
      .attr("stroke-width", "1")
      .attr("stroke-linejoin", "round")
      .attr(
        "d",
        d3
          .line()
          .curve(d3.curveBasis)
          .x(d => xScale(d[0]) + 200)
          .y(d => yScale(0))
      )

    pathUnemploymentGroup
      .transition()
      .duration(3000)
      .ease(d3.easeBounceIn)
      .attr(
        "d",
        d3
          .line()
          .curve(d3.curveBasis)
          .x(d => xScale(d[0]))
          .y(d => yScale(d[1]))
      )

    let densityEmploymentGroup = chartGroup
      .append("g")
      .attr("class", "densityEmploymentGroup")
      .attr("transform", `translate(${0},${0})`)

    let pathEmploymentGroup = densityEmploymentGroup
      .append("path")
      .datum(densityEmployment)
      .attr("fill", "lightblue")
      .attr("opacity", ".7")
      .attr("stroke", "#d8d8d8")
      .attr("stroke-width", "1")
      .attr("stroke-linejoin", "round")
      .attr(
        "d",
        d3
          .line()
          .curve(d3.curveBasis)
          .x(d => xScale(d[0]) - 200)
          .y(d => yScale(0))
      )

    pathEmploymentGroup
      .transition()
      .duration(3000)
      .ease(d3.easeQuadIn)
      .attr(
        "d",
        d3
          .line()
          .curve(d3.curveBasis)
          .x(d => xScale(d[0]))
          .y(d => yScale(d[1]))
      )
  }, [data, toggleChange])
  return (
    <div className="chart" id="characteristic_province">
      <header>
        <h4>
          Labour force characteristics by province - Expanded definition of
          unemployment (2010 - 2020)
        </h4>
        <label>
          <select
            defaultValue={toggleChange}
            onBlur={event => setToggleChange(event.target.value)}
          >
            <option value="Eastern Cape">Eastern Cape</option>
            <option value="Free State">Free State</option>
            <option value="Gauteng">Gauteng</option>

            <option value="KwaZulu-Natal">KwaZulu-Natal</option>
            <option value="Limpopo">Limpopo</option>
            <option value="Mpumalanga">Mpumalanga</option>

            <option value="North West">North West</option>
            <option value="Northern Cape">Northern Cape</option>
            <option value="Western Cape">Western Cape</option>
          </select>
          <span className="down"></span>
        </label>
      </header>
    </div>
  )
}
