import React, { useEffect } from "react";
import * as d3 from "d3";

export default function GDPPopulationChart({ dataset }) {
  useEffect(() => {
    // set the dimensions and margins of the graph
    var margin = { top: 20, right: 30, bottom: 30, left: 50 },
      width = 700,
      height = 300;

    d3.select("#gdp_population").append("svg").remove();

    let svg = d3
      .select("#gdp_population")
      .append("svg")
      .attr("viewBox", [
        0,
        0,
        width + 2 * margin.right,
        height + 3 * margin.bottom,
      ]);

    let chartGroup = svg
      .append("g")
      .attr("transform", `translate(${margin.left},${margin.top})`);

    let xScale = d3
      .scaleLinear()
      .domain([
        d3.min(dataset, (d) => d.price),
        d3.max(dataset, (d) => d.price) + 10000,
      ])
      .range([margin.right / 2, width - margin.right]);

    let yScale = d3
      .scaleLinear()
      .domain([0, d3.max(dataset, (d) => d.employed) + 100])
      .range([height, 0]);

    let radius = d3
      .scaleSqrt()
      .domain(d3.extent(dataset, (d) => d.price))
      .range([0, 4]);

    let colors = d3
      .scaleSequential()
      .domain([0, 10])
      .interpolator(d3.interpolateRainbow);

    let xAxis = d3
      .axisBottom(xScale)
      .tickFormat((d) => {
        return parseInt(d);
      })
      .tickPadding(10)
      .ticks(5);
    let yAxis = d3
      .axisLeft(yScale)
      .tickFormat((d) => {
        return parseInt(d);
      })
      .tickPadding(10)
      .ticks(5);

    let xAxisGroup = chartGroup
      .append("g")

      .attr("class", "xAxisGroup")
      .attr("transform", `translate(${0},${height + margin.top})`);

    let yAxisGroup = chartGroup
      .append("g")
      .attr("class", "yAxisGroup")
      .attr("transform", `translate(${0},${0})`);

    xAxis(xAxisGroup);
    yAxis(yAxisGroup);

    let xAxiGrids = d3
      .axisBottom(xScale)
      .tickFormat("")
      .tickSize(-height - margin.top)
      .ticks(7);
    let yAxisGrids = d3
      .axisLeft(yScale)
      .tickFormat("")
      .ticks(6)
      .tickSize(-width);

    let xAxisGridsGroup = chartGroup
      .append("g")
      .attr("class", "xAxisGridsGroup")
      .attr("transform", `translate(${0},${height + margin.top})`);

    let yAxisGridsGroup = chartGroup
      .append("g")
      .attr("class", "xAxisGridsGroup")
      .attr("transform", `translate(${0},${0})`);

    xAxiGrids(xAxisGridsGroup);
    yAxisGrids(yAxisGridsGroup);

    let plotGroup = chartGroup
      .append("g")
      .attr("class", "plotGroup")
      .attr("transform", `translate(${margin.right},${0})`);

    let circlePlotGroup = plotGroup
      .selectAll("circle")
      .data(dataset)
      .enter()
      .append("circle")
      .attr("fill", (d, i) => colors(i))
      .attr("opacity", 0.7)
      .attr("cx", (d) => xScale(0))
      .attr("cy", (d) => yScale(0))
      .attr("r", 3);

    circlePlotGroup
      .transition()
      .duration(500)
      .attr("cx", (d) => xScale(d.price))
      .attr("cy", (d) => yScale(d.employed));

    circlePlotGroup
      .transition()
      .duration(500)
      .delay(500)
      .ease(d3.easeLinear)
      .attr("r", (d) => radius(d.population));

    let foreignObject = chartGroup
      .selectAll("foreignObject")
      .data(dataset)
      .enter()
      .append("foreignObject")
      .filter(
        (d) =>
          d.province_name === "Gauteng" ||
          d.province_name === "Eastern Cape" ||
          d.province_name === "KwaZulu-Natal"
      )
      .style("width", "80px")
      .style("height", "120px")
      .attr("x", (d) => {
        return xScale(d.price - 1800);
      })
      .attr("y", (d) => {
        return yScale(d.employed + margin.bottom);
      });

    foreignObject
      .append("xhtml:div")
      .text((d) => {
        return `${d.province_name} ${d.population.toFixed(0)}`;
      })
      .style("text-align", "center")
      .style("font-size", ".75rem")
      .style("white-space", "text-wrap");

    chartGroup
      .append("foreignObject")
      .style("color", "black")
      .style("width", "150px")
      .style("height", "300px")
      .attr("x", xScale(-25000))
      .attr("y", yScale(550))
      .attr("transform", `rotate(-90)`)
      .append("xhtml:div")
      .text("2019 Average Employment By Province (Thousands)")
      .style("text-align", "center")
      .style("font-size", ".75rem")
      .style("white-space", "text-wrap");

    chartGroup
      .append("foreignObject")
      .style("color", "black")
      .style("width", "180px")
      .style("height", "300px")
      .attr("x", xScale(42000))
      .attr("y", yScale(80))
      // .attr("transform", `rotate(-90)`)
      .append("xhtml:div")
      .text("2019 Average GDP at constant prices By Province (R Millions)")
      .style("text-align", "center")
      .style("text-align", "left")
      .style("font-size", ".75rem")
      .style("white-space", "text-wrap");

    const bubbleLabels = [
      d3.min(dataset, (d) => d.population),
      d3.mean(dataset, (d) => d.population),
      d3.max(dataset, (d) => d.population),
    ];

    yAxisGrids(yAxisGridsGroup);

    let bubbleLabeltGroup = chartGroup
      .append("g")
      .attr("class", "bubbleLabeltGroup")
      .attr("transform", `translate(${xScale(92000)},${yScale(140)})`);

    let circleLabelGroup = bubbleLabeltGroup
      .selectAll("circle")
      .data(bubbleLabels)
      .enter()
      .append("circle")
      .attr("fill", "none")
      .attr("stroke", "black")
      .attr("cy", (d, i) => -23 * i)
      .attr("r", 0);

    circleLabelGroup
      .transition()
      .duration(1000)
      .attr("r", (d) => radius(d));

    let textLabels = ["Min", "Mean", "Max"];

    bubbleLabeltGroup
      .selectAll("text")
      .data(textLabels)
      .enter()
      .append("text")
      .text((d) => d)
      .attr("x", xScale(18000))
      .attr("y", (d, i) => -24 * i);

    let lineLabelGroup = bubbleLabeltGroup
      .selectAll("line")
      .data(textLabels)
      .enter()
      .append("line")
      .text((d) => d)
      .attr("stroke", "black")
      .attr("stroke-dasharray", "2")
      .attr("x1", 0)
      .attr("y1", (d, i) => -24 * i)
      .attr("y2", (d, i) => -24 * i)
      .attr("x2", (d, i) => xScale(18000));

    lineLabelGroup
      .transition()
      .duration(1000)
      .attr("x1", (d, i) => xScale(18000))
      .attr("x2", (d, i) => xScale(6500 + 3800 * i));

    chartGroup
      .append("foreignObject")
      .style("color", "black")
      .style("width", "120px")
      .style("height", "300px")
      .attr("x", xScale(82000))
      .attr("y", yScale(80))
      // .attr("transform", `rotate(-90)`)
      .append("xhtml:div")
      .text("2019 Population By Province (Thousands)")
      .style("text-align", "center")
      .style("font-size", ".75rem")
      .style("white-space", "text-wrap");
  }, [dataset]);
  return <div id="gdp_population" className="chart"></div>;
}
