import React from "react"
import { BsFillGridFill, BsFillBarChartFill } from "react-icons/bs"
import { MdBubbleChart, MdMultilineChart } from "react-icons/md"
import { TiChartArea } from "react-icons/ti"

import * as stylesSidebar from "./stats-sidebar.module.css"

export default function Sidebar({ onToggle, toggle }) {
  return (
    <aside className={stylesSidebar.statsSidebar}>
      <nav>
        <ul>
          <li
            className={!toggle ? stylesSidebar.active : "none"}
            aria-hidden="true"
          >
            <BsFillGridFill />
          </li>
          <li
            className={toggle === "histogram" ? stylesSidebar.active : "none"}
            onClick={() => onToggle("histogram")}
            aria-hidden="true"
          >
            <BsFillBarChartFill />
          </li>
          <li
            className={toggle === "bubble" ? stylesSidebar.active : "none"}
            onClick={() => onToggle("bubble")}
            aria-hidden="true"
          >
            <MdBubbleChart />
          </li>
          <li
            className={toggle === "area" ? stylesSidebar.active : "none"}
            onClick={() => onToggle("area")}
            aria-hidden="true"
          >
            <TiChartArea />
          </li>
          <li
            className={toggle === "density" ? stylesSidebar.active : "none"}
            onClick={() => onToggle("density")}
            aria-hidden="true"
          >
            <MdMultilineChart />
          </li>
        </ul>
      </nav>
    </aside>
  )
}
