import React, { useEffect } from "react";
import * as d3 from "d3";

export default function EmploymentProvinceChart({ data }) {
  useEffect(() => {
    // set the dimensions and margins of the graph
    var margin = { top: 20, right: 20, bottom: 30, left: 30 },
      width = 700,
      height = 300;
    // offsetX = 22;

    // let dataset = data.filter((d) => d.year === 2020);

    let dataset = data.slice(-data.length + 1).map((d, i) => {
      return {
        qq_employment_change:
          ((d.employed - data[i].employed) / data[i].employed) * 100,
        qq_gdp_change: ((d.price - data[i].price) / data[i].price) * 100,
      };
    });

    d3.select("#employment_province").append("svg").remove();

    let svg = d3
      .select("#employment_province")
      .append("svg")
      .attr("viewBox", [
        0,
        0,
        width + 2 * margin.right,
        height + 3 * margin.bottom,
      ]);

    let chartGroup = svg
      .append("g")
      .attr("transform", `translate(${margin.left},${margin.top})`);

    let xScale = d3
      .scaleLinear()
      .domain([-10, 10])
      .range([0, width - margin.right]);

    let histogram = (value) =>
      d3
        .bin()
        .value((d) => d[value])
        .domain(xScale.domain())
        .thresholds(xScale.ticks(70));

    let keys = ["qq_employment_change", "qq_gdp_change"];

    let yScale = d3
      .scaleLinear()
      .domain([0, d3.max(histogram(keys[1])(dataset), (d) => d.length)])
      .range([height, 0]);

    let colors = d3
      .scaleOrdinal()
      .domain([0, 50, 100])
      .range(["lightblue", "orange"]);

    let xAxis = d3.axisBottom(xScale).tickPadding(10);
    let yAxis = d3.axisLeft(yScale).tickPadding(10).ticks(6);

    let xAxisGroup = chartGroup
      .append("g")
      .attr("class", "xAxisGroup")
      .attr("transform", `translate(${margin.right},${height + margin.top})`);

    let yAxisGroup = chartGroup
      .append("g")
      .attr("class", "yAxisGroup")
      .attr("transform", `translate(${0},${0})`);

    xAxis(xAxisGroup);
    yAxis(yAxisGroup);

    let xAxiGrids = d3
      .axisBottom(xScale)
      .tickFormat("")
      .tickSize(-height - 5);
    let yAxisGrids = d3
      .axisLeft(yScale)
      .tickFormat("")
      .ticks(6)
      .tickSize(-width);

    let xAxisGridsGroup = chartGroup
      .append("g")
      .attr("class", "xAxisGridsGroup")
      .attr("transform", `translate(${margin.right},${height + margin.top})`);

    let yAxisGridsGroup = chartGroup
      .append("g")
      .attr("class", "xAxisGridsGroup")
      .attr("transform", `translate(${0},${0})`);

    xAxiGrids(xAxisGridsGroup);
    yAxisGrids(yAxisGridsGroup);

    const getBins = (value, color) => {
      let groupBins = chartGroup
        .append("g")
        .attr("class", value)
        .attr("transform", `translate(${margin.right},${0})`);

      let rectGroup = groupBins
        .selectAll("rect")
        .data(histogram(value)(dataset))
        .enter()
        .append("rect")
        .attr("fill", color)
        .attr("width", (d) => xScale(d.x1) - xScale(d.x0) - 1)
        .attr("height", 0)
        .attr("x", 1)
        .attr(
          "transform",
          (d) => `translate(${xScale(d.x0)}, ${yScale(d.length)})`
        );

      rectGroup
        .transition()
        .duration(1000)
        .ease(d3.easeElasticIn)
        .attr("height", (d) => height - yScale(d.length));
    };

    keys.map((key, i) => getBins(key, colors(i)));
  }, [data]);

  return <div id="employment_province" className="attendance_chart"></div>;
}
