import { graphql } from "gatsby"
import React, { useState } from "react"
import * as d3 from "d3"
import {
  BsFillPersonCheckFill,
  BsFillBarChartFill,
  BsFillPersonDashFill,
} from "react-icons/bs"
import Sidebar from "../components/stats/stats-sidebar"
import EmploymentIndustry from "../components/stats/employment-industry"
import UnploymentProvince from "../components/stats/UnploymentProvince"
import EmploymentProvince from "../components/stats/employment-province"
import GDPPopulation from "../components/stats/GDPPopulation"
import LabourCharacteristicProvince from "../components/stats/LabourCharacteristicProvince"

import * as statsStyles from "./stats.module.css"

export default function Stats({ data }) {
  const [toggle, setToggle] = useState(false)

  const employment = data.allEmployment.edges.map(({ node }) => node)
  const unemployment = data.allUnemploymentPerCapita.edges.map(
    ({ node }) => node
  )
  const gdp = data.allGdp.edges.map(({ node }) => node)
  const provinces = data.allProvincePerCapita.edges.map(({ node }) => node)

  let groupByQuarterYear = gdp.reduce((prev, cur) => {
    let concatQuaterWithYear = cur.industry + "" + cur.quarter + "" + cur.year
    if (!prev[concatQuaterWithYear]) {
      prev[concatQuaterWithYear] = []
    }

    // if (propertyName === "gdp") {
    prev[concatQuaterWithYear].push({
      industry: cur.industry,
      quarter: cur.quarter,
      year: cur.year,
      price: parseFloat(cur.price.toFixed(2)),
    })

    return prev
  }, {})

  let groupByEmploymentQuarterYear = employment.reduce((prev, cur) => {
    let concatQuaterWithYear = cur.industry + "" + cur.quarter + "" + cur.year
    if (!prev[concatQuaterWithYear]) {
      prev[concatQuaterWithYear] = []
    }
    prev[concatQuaterWithYear].push({
      employed: parseInt(cur.employed.toFixed(0)),
    })

    return prev
  }, {})

  let dataset = Object.keys(groupByQuarterYear).map(key => {
    return {
      ...groupByQuarterYear[key][0],
      ...groupByEmploymentQuarterYear[key][0],
    }
  })

  let datasetQuater = dataset.filter(d => {
    return d.year === 2020
  })

  let groupQuaters = datasetQuater.reduce((prev, cur) => {
    if (!prev[cur.quarter]) {
      prev[cur.quarter] = []
    }

    prev[cur.quarter].push(cur)

    return prev
  }, {})

  let totalEmployment = Object.keys(groupQuaters).map((key, i) => {
    return {
      quarter: key,
      avg: d3
        .mean(groupQuaters[key], d => {
          return d.employed
        })
        .toFixed(0),
    }
  })

  const onToggle = args => {
    setToggle(args)
  }

  let colors = d3
    .scaleSequential()
    .domain([0, 10])
    .interpolator(d3.interpolateRainbow)

  return (
    <section className={statsStyles.stats}>
      {totalEmployment.length && (
        <>
          {!toggle && (
            <ul className={statsStyles.numbers}>
              <li>
                <span>{totalEmployment[0].avg}</span>
                <span>Q1 Average</span>
              </li>
              <li>
                <span>{totalEmployment[1].avg}</span>
                <span>Q2 Average</span>
              </li>
              <li>
                <span>{totalEmployment[2].avg}</span>
                <span>Q3 Average</span>
              </li>
              <li>
                <span>{totalEmployment[3].avg}</span>
                <span>Q4 Average</span>
              </li>
            </ul>
          )}

          {toggle === "histogram" && (
            <ul className={statsStyles.histogram}>
              <li>
                <span>{<BsFillPersonCheckFill />}</span>
                <ul>
                  <li>Employment By Industry</li>
                  <li>Q/Q Change (2010 - 2020)</li>
                </ul>
              </li>
              <li>
                <span>{<BsFillBarChartFill />}</span>
                <ul>
                  <li>Gross Domestic Product</li>
                  <li>Q/Q Change (2010 - 2020)</li>
                </ul>
              </li>
            </ul>
          )}

          {toggle === "density" && (
            <ul className={statsStyles.histogram}>
              <li>
                <span>{<BsFillPersonCheckFill />}</span>
                <ul>
                  <li>Employment By Province</li>
                  <li>Population Ratio (2010 - 2020)</li>
                </ul>
              </li>
              <li>
                <span>
                  <BsFillPersonDashFill />
                </span>

                <ul>
                  <li>Unemployment By Province</li>
                  <li>Expanded Rate (2010 - 2020)</li>
                </ul>
              </li>
            </ul>
          )}

          {(toggle === "bubble" || toggle === "area") && (
            <ul className={statsStyles.bubble}>
              {provinces.map((d, i) => (
                <li key={i}>
                  <span style={{ backgroundColor: colors(i) }}></span>
                  <span>{d.province_name}</span>
                </li>
              ))}
            </ul>
          )}
        </>
      )}
      <div>
        <header>
          <hgroup>
            <h1>Stats SA (Statistical Release P0211)</h1>
            <h4>Quarterly Labour Force Survey - Q4 2020</h4>
          </hgroup>
          <h4>
            <span>
              Employment By{" "}
              {!toggle || toggle === "histogram" ? "Industry" : "Province"}
            </span>
            <span></span>
          </h4>
        </header>
        {!toggle && <EmploymentIndustry dataset={dataset} />}
        {toggle === "histogram" && <EmploymentProvince dataset={dataset} />}
        {toggle === "bubble" && <GDPPopulation dataset={provinces} />}
        {toggle === "area" && <UnploymentProvince dataset={unemployment} />}
        {toggle === "density" && (
          <LabourCharacteristicProvince dataset={unemployment} />
        )}
      </div>
      <Sidebar onToggle={onToggle} toggle={toggle} />
    </section>
  )
}

export const query = graphql`
  query {
    allEmployment {
      edges {
        node {
          industry
          employed
          quarter
          year
        }
      }
    }
    allGdp {
      edges {
        node {
          industry
          quarter
          year
          price
        }
      }
    }
    allUnemploymentPerCapita {
      edges {
        node {
          province
          date
          quarter
          category
          unemployed
        }
      }
    }
    allProvincePerCapita {
      edges {
        node {
          province_id
          province_name
          population
          price
          employed
        }
      }
    }
  }
`
