import React, { useEffect, useState } from "react"
import * as d3 from "d3"

export default function EmploymentIndustryChart({ data }) {
  const [update, setUpdate] = useState("QR01")
  const [year, setYear] = useState(new Date().getFullYear() - 1)
  // const [dataset] = useState(data.filter((d) => d.quarter === update));

  useEffect(() => {
    var margin = { top: 40, right: 30, bottom: 30, left: 210 },
      width = 600,
      height = 240,
      offsetX = 100,
      offsety = 5

    let svg = d3
      .select("#employment_industry_chart")
      .append("svg")
      .attr("viewBox", [0, 0, width + margin.right, height + 2 * margin.bottom])

    let chartGroup = svg
      .append("g")
      .attr("transform", `translate(${margin.left},${2 * margin.bottom})`)

    const checkQ = (d, value) => {
      if (update === "QR01") {
        return `${"QR01"} ${year - 1}`
      }

      if (update === "QR02") {
        return `${"QR01"} ${year}`
      }

      if (update === "QR03") {
        return `${"QR02"} ${year}`
      }

      if (update === "QR04") {
        return `${"QR03"} ${year}`
      }

      return null
    }

    let dataset = data.filter(d => {
      return d.quarter === `${update}` && d.year === year
    })

    let prevDataset = data.filter(
      d =>
        d.quarter === checkQ().substring(0, 4) &&
        d.year === parseInt(checkQ().substring(5, 9))
    )

    let xScale = d3
      .scaleLinear()
      .domain([0, 3700])
      .range([0, width - margin.left - offsetX])

    let yScale = d3
      .scaleBand()
      .domain(dataset.map(d => d.industry))
      .range([height, 0])

    let yAxis = d3.axisLeft(yScale).tickPadding(10)

    let yAxisGroup = chartGroup
      .append("g")
      .attr("class", "yAxisGroup")
      .attr("transform", `translate(${0},${0})`)

    yAxis(yAxisGroup)

    let areaChartGroup = chartGroup
      .append("g")
      .attr("class", "areareaChartGroupaChart")
      .attr("transform", `translate(${offsetX},${0})`)

    const colors = d3
      .scaleSequential()
      .domain([0, 10])
      .interpolator(d3.interpolateRainbow)

    let rectChart = areaChartGroup
      .selectAll("rect")
      .data(dataset)
      .enter()
      .append("rect")
      .attr("width", 0)
      .attr("height", yScale.bandwidth() - 1)
      .style("stroke", "none")
      .attr("x", 0)
      .attr("y", d => {
        return yScale(d.industry)
      })
      .attr("fill", (d, i) => {
        return colors(i)
      })

    rectChart
      .transition()
      .duration(500)
      .ease(d3.easeLinear)
      .attr("width", d => {
        return xScale(d.employed)
      })
      .attr("x", yScale(0))

    chartGroup
      .append("rect")
      .attr("width", 100)
      .attr("height", yScale.bandwidth())
      .attr("x", 0)
      .attr("y", -yScale.bandwidth() * 2)
      .attr("fill", "none")

    chartGroup
      .append("text")
      .attr("text-anchor", "middle")
      .attr("y", -margin.top / 1.25)
      .attr("x", margin.right * 1.6)
      .text("Thousands")

    chartGroup
      .append("rect")
      .attr("width", 315)
      .attr("height", yScale.bandwidth())
      .attr("x", 100)
      .attr("y", -yScale.bandwidth() * 2)
      .attr("fill", "none")

    chartGroup
      .append("text")
      .attr("text-anchor", "middle")
      .attr("y", -margin.top / 1.25)
      .attr("x", 315 / 1.25)
      .text("Quarterly 2020")

    areaChartGroup
      .selectAll("text")
      .data(dataset)
      .enter()
      .append("text")
      .attr("text-anchor", "middle")
      .style("font-size", ".675rem")
      .style("fill", (d, i) => {
        return d.employed - prevDataset[i].employed < 0 ? "red" : "green"
      })
      .style("opacity", ".5")
      .attr("transform", `translate(180,${0}) rotate(180deg)`)
      .attr("y", d => yScale(d.industry) + margin.top / 2.5)
      .attr("x", d => xScale(d.employed) + margin.right / 2)
      .text((d, i) => {
        return `${d.employed - prevDataset[i].employed}k`
      })

    let yAxisGridGroup = chartGroup
      .append("g")
      .attr("class", "yAxisGridGroup")
      .attr("transform", `translate(${0},${11})`)

    let yAxisGrid = d3
      .axisLeft(yScale)
      .tickFormat("")
      .tickSize(-width / 1.45)
      .tickPadding(10)

    yAxisGrid(yAxisGridGroup)

    let titles = ["Expected", "Actual"]

    let titleGroup = chartGroup
      .append("g")
      .attr("class", "titleGroup")
      .attr(
        "transform",
        `translate(${offsetX - 100},${-margin.top / 2 - offsety})`
      )

    titleGroup
      .selectAll("rect")
      .data(titles)
      .enter()
      .append("rect")
      .attr("fill", "none")
      .attr("stroke", "grey")
      .attr("width", 50)
      .attr("height", yScale.bandwidth())
      .attr("y", 0)
      .attr("x", (d, i) => i * 50)
      .text(d => d.industry)

    titleGroup
      .selectAll("text")
      .data(titles)
      .enter()
      .append("text")
      .attr("text-anchor", "middle")
      .attr("y", margin.top / 2.5)
      .attr("x", (d, i) => i * 50 + margin.right - 5)
      .text(d => d)

    let expected = chartGroup
      .append("g")
      .attr("class", "expected")
      .attr("transform", `translate(${0},${0})`)

    expected
      .selectAll("rect")
      .data(dataset)
      .enter()
      .append("rect")
      .attr("fill", "none")
      .attr("stroke", "grey")
      .attr("width", 50)
      .attr("height", yScale.bandwidth())
      .attr("y", d => yScale(d.industry))
      .attr("x", d => xScale(d.industry))
      .text(d => d.industry)

    expected
      .selectAll("text")
      .data(dataset)
      .enter()
      .append("text")
      .attr("stroke", "grey")
      .attr("text-anchor", "middle")
      .attr("y", d => yScale(d.industry) + margin.top / 2.5)
      .attr("x", margin.right - 5)
      .text(d => "-")

    let actual = chartGroup
      .append("g")
      .attr("class", "actual")
      .attr("transform", `translate(${50},${0})`)

    actual
      .selectAll("rect")
      .data(dataset)
      .enter()
      .append("rect")
      .attr("fill", "none")
      .attr("stroke", "grey")
      .attr("width", 50)
      .attr("height", yScale.bandwidth())
      .attr("y", d => yScale(d.industry))
      .attr("x", d => xScale(d.industry))
      .text(d => d.industry)

    actual
      .selectAll("text")
      .data(dataset)
      .enter()
      .append("text")
      .attr("text-anchor", "middle")
      .attr("y", d => yScale(d.industry) + margin.top / 2.5)
      .attr("x", margin.right - 5)
      .text(d => d.employed.toFixed(0))

    let keys = ["QR01", "QR02", "QR03", "QR04"]

    let xScaleKey = d3.scaleBand().domain(keys).range([0, 315])

    let xAxisGridGroup = chartGroup
      .append("g")
      .attr("class", "xAxisGridGroup")
      .attr("transform", `translate(${100},${0})`)

    let xAxisGrid = d3
      .axisBottom(xScale)
      .tickFormat("")
      .ticks(5)
      .tickValues([1000, 2000, 3000, 4000])
      .tickSize(height)

    xAxisGrid(xAxisGridGroup)

    let changeInput = chartGroup
      .append("g")
      .attr("class", "changeInput")
      .attr(
        "transform",
        `translate(${margin.left - offsetX - 10},${-yScale.bandwidth()})`
      )
      .selectAll("g")
      .data(keys)
      .enter()
      .append("g")
      .on("click", function (event, d) {
        event.preventDefault()

        d3.select(this).attr("class", "active")
        d3.select("#employment_industry_chart").append("svg").remove()

        setUpdate(d)
        setYear(2020)
      })

    changeInput
      .append("rect")
      .attr("fill", "white")
      .attr("stroke", "grey")
      .attr("width", d => xScaleKey.bandwidth())
      .attr("height", yScale.bandwidth())
      .attr("y", 0)
      .attr("x", d => xScaleKey(d))

    changeInput
      .append("text")
      .attr("text-anchor", "middle")
      .style("font-size", ".765rem")
      .attr("y", margin.top / 2.5)
      .attr("x", (d, i) => i * 77 + margin.right + 10)
      .text(d => {
        if (d === "QR01") {
          return "Q1"
        }

        if (d === "QR02") {
          return "Q2"
        }

        if (d === "QR03") {
          return "Q3"
        }

        if (d === "QR04") {
          return "Q4"
        }
      })
  })

  return (
    <div
      id="employment_industry_chart"
      className="employment-industry-chart"
    ></div>
  )
}
